import React from 'react';
import {TalkEventDetails} from '../../DetailedEvent';
import poster from '../../../../Photos/acm2020/speed-mentorship-2020.jpg'
import octocat from '../../../../Photos/octocat-speed-mentorship2020.png'

function importAll(r) {
  return r.keys().map(r);
}

const images = [];

const LinkArray = {title: 'Contact me',
                   links: [
                     {
                       url: 'stella.rouzi at gmail dot com',
                       text: 'Email'},
                     { url: 'https://www.facebook.com/differentreality/', text: 'Facebook'},
                     { url: 'https://www.linkedin.com/in/differentreality/', text: 'LinkedIn'}
                    ]}
const GitArray = { title: 'All about GIT', links: [
                   { text: 'How to start, or copy, a git project', url: 'https://differentreality.medium.com/git-101-get-started-with-a-project-77995b3957cf' },
                   { text: 'Git Cheatsheet', url: 'http://localhost:3001/#/workshops/topics/git/GitZeroToHero'}
                 ]}
const GsocArray = {title: 'Mentorship', links: [
  { text: 'What is GSoC', url: 'https://summerofcode.withgoogle.com/how-it-works/' },
  { text: 'Who  can participate', url: 'https://summerofcode.withgoogle.com/get-started/'}
]}

const imageCaptions=[]

const eventData =
    {
        title:'ACM Speed Mentorship 2020',
        date:'12/12/2020',
        details: <div>
          <p> Huge fan of open source, and volunteering. I love sharing knowledge - feel free to ask me anything! </p>
          <p><ul>
            <li>Web development with Ruby on Rails (the official guides are great) <a href="http://guides.rubyonrails.org">guides.rubyonrails.org</a></li>
            <li>Git - a must tool, and your best friend (see the resources below) </li>
            <li>Looking to contribute to an <b>open source project</b>, improve your <b>coding skills</b>, and learn how to bring a project idea to life? You can get help, and <b>mentorship</b> every step of the way through Google Summer of Code!</li></ul>
          </p>
          <div className='PhotoContainer md-6' >
            <div className='PhotoContainer__stella' >
              <img alt='one of my octocats' src={octocat} className='PhotoContainer__octocat__image' style={{ width: '100%' }} onClick={()=>this.openLightboxOnSlide(1)}/>
            </div>
          </div>
        </div>,
        group:'talk',
        topic:'Mentorship',
        poster: poster,
        socialEvent:'https://www.facebook.com/events/291941072240888',
        slides: '',

        links:[LinkArray, GitArray, GsocArray],
        images,
        imageCaptions
    }


const acm2020 = (props)=><TalkEventDetails mobile={props.mobile} group={eventData.group} data={eventData}/>

export default acm2020;
